import "../public/css/tailwind.css";
import { withRouter } from "next/router";
import { useState } from "react";
import Link from "next/link";
import MenuLink from "./menu-link";
import SubMenuLink from "./menu-link-children";

const menuItems = activeRoute =>
  [
    //  { page: "Ordering", route: "/ordering" },
    {
      page: "Products",
      route: "/products",
      children: [
        {
          prefix: "Products",
          page: "Cancer panel",
          route: "/products/cancer-panel"
        }
      ]
    },
    { page: "Technology", route: "/technology" },
    { page: "News", route: "/news" },
    {
      page: "About Us",
      route: "/about",
      children: [
        { prefix: "About Us", page: "Contact Us", route: "/about/contact" },
        { prefix: "About Us", page: "Find Us", route: "/about/find-us" },
        { prefix: "About Us", page: "Our Vision", route: "/about/vision" }
      ]
    }
  ]
    .map(itm => activatePage(activeRoute, itm))
    .map(linkUp);

const activatePage = (activeRoute, menuItem) => {
  if (typeof menuItem.children !== "undefined") {
    menuItem.children = menuItem.children.map(itm =>
      activatePage(activeRoute, itm)
    );
  }
  menuItem.active = menuItem.route === activeRoute;
  return menuItem;
};

const linkUp = menuItem =>
  typeof menuItem.children !== "undefined" ? (
    <SubMenuLink key={menuItem.route} menuItem={menuItem} />
  ) : (
    <MenuLink key={menuItem.route} menuItem={menuItem} />
  );

export default withRouter(({ router }) => {
  const [showMenu, setShowMenu] = useState(new Boolean(false));
  const menu = menuItems(router.pathname);
  return (
    <div className="w-full lg:max-w-5xl lg:container lg:mx-auto font-menu">
      <nav className="flex items-center justify-between flex-wrap lg:flex-no-wrap p-6 lg:p-0 lg:pt-6 text-vesicode-grey uppercase">
        <div className="flex-shrink-0 mr-6 pb-8">
          <Link href="/">
            <a className="fill-current">
              <img
                className="pb-2"
                src="/img/vesicode-logo.svg"
                alt="Vesicode AB"
                title="Vesicode AB"
              />
            </a>
          </Link>
        </div>
        <div className="block lg:hidden">
          <button
            onClick={() => setShowMenu(new Boolean(!showMenu.valueOf()))}
            className="flex items-center px-3 py-2 border rounded-sm text-vesicode-green-light border-vesicode-green-light hover:text-white hover:border-white"
          >
            <svg
              className="fill-current h-5 w-5"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <title>Menu</title>
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
            </svg>
          </button>
        </div>
        <div
          className={
            "w-full block flex-grow lg:flex lg:flex-row lg:flex-grow-0 lg:items-center lg:w-auto lg:visible " +
            (showMenu.valueOf() ? "visible" : "hidden")
          }
        >
          <div className="lg:flex-grow lg:flex-row lg:flex">{menu}</div>
        </div>
      </nav>
    </div>
  );
});
